



























































































































































import { Vue, Component, Prop } from 'vue-property-decorator';
import { editHero, addHero, getHeroInfo, getHeroList } from '@/api/hero';
import { getCategoryList } from '@/api/category';
import { getItemList } from '@/api/item';

@Component
// 导入的其他文件 例如：import moduleName from 'modulePath';
export default class HerosEdit extends Vue {
  public model: HeroModel = {
    name: '',
    title: '',
    categories: [],
    avatar: '',
    banner: '',
    scores: {},
    item1: [],
    item2: [],
    skills: [],
    usageTips: '',
    battleTips: '',
    teamTips: '',
    partners: [],
  };
  public categories: object[] = [];
  public items: object[] = [];
  public heros: object[] = [];
  @Prop({type: String}) public id!: string;
  public created() {
    this.fetchCategories();
    this.fetchItems();
    this.fetchHeros();
    this.id && this.fetch();
  }
  // 通过id将新增和编辑页面作区分
  public async save() {
    let res;
    if (this.id) {
      res = await editHero(this.id, this.model);
    } else {
      res = await addHero(this.model);
    }
    this.$router.push('/heros/list');
    this.$message({
      type: 'success',
      message: '保存成功',
    });
  }
  public async fetch() {
    const res = await getHeroInfo(this.id);
    this.model = Object.assign({}, this.model, res.data);
  }
  public async fetchCategories() {
    const res: any = await getCategoryList('英雄分类');
    this.categories = res.data;
  }
  public async fetchItems() {
    const res: any = await getItemList();
    this.items = res.data;
  }
  public afterUploadIcon(res: any) {
    this.model.avatar = res.url;
  }
  public afterUploadBanner(res: any) {
    this.model.banner = res.url;
  }
  public async fetchHeros() {
    const res: any = await getHeroList();
    this.heros = res.data;
  }
}
