































import { Vue, Component } from 'vue-property-decorator';
import { getCategoryList, deleteCategory } from '@/api/category';

@Component
export default class CategoriesList extends Vue {
  public items: object[] = [];
  public created() {
    this.fetch();
  }
  public async fetch() {
    const res: any = await getCategoryList();
    this.items = res.data;
  }
  public async deleteItem(row: CategoriesItemModel) {
    this.$confirm(`确认删除分类${row.name}`, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
    }).then(async () => {
      const res = await deleteCategory(row._id);
      this.$message({
        type: 'success',
        message: res.data.msg,
      });
      this.fetch();
    }).catch(() => {
      console.log('cancel');
    });
  }
}
